<template>
  <div class="nursingrecordlist">
    <nursingrecordPrintOption ref="enterEdit"></nursingrecordPrintOption>

    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>護理紀錄</span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
                @keyup.stop="search()"
              ></v-text-field>

              <popupadd @getAllData="getAllData" ref="editProcess"></popupadd>

              <v-btn class="primary mx-1" @click="print">
                <v-icon class="mx-1">mdi-printer</v-icon>列印
              </v-btn>
            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="viewData"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead style="background-color: #e3f2fd;">
                      <tr>
                        <th></th>
                        <th>日期</th>
                        <th>時間</th>
                        <th>項目</th>
                        <th>護理紀錄內容</th>
                        <th>紀錄人</th>
                        <th>修改紀錄</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="(item,index) in props.items"
                        :key="index"
                        :class="[{'red lighten-2':item.focus=='true'}]"
                      >
                        <td>
                          <v-menu transition="scale-transition" offset-y>
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="edit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="del(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                              <!-- <v-list-item @click.stop="print(item)">
                                <v-list-item-title>列印</v-list-item-title>
                              </v-list-item>-->
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.time}}</td>
                        <td>{{item.project}}</td>
                        <td>{{item.nursingrecord_content}}</td>

                        <td>{{item.create_man}}</td>
                        <td>{{item.edit_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>{{items}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import popupadd from "@/views/document/NursingrecordAdd";
import nursingrecordPrintOption from "@/components/nursingrecordPrintOption"; //列印藥單前的內容設定
import dayjs from "dayjs";

export default {
  components: { popupadd, nursingrecordPrintOption },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      viewData: [], //顯示的資料
      serachKey: "",
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
    };
  },

  mounted() {
    this.getAllData();
  },

  methods: {
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey,
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/nursingrecord",
          qs.stringify(postData)
        )
        .then((rs) => {
          // console.log(rs.data)
          this.items = this.viewData = rs.data.map((i) => {
            var newItem = JSON.parse(i.datalist);
            newItem.snkey = i.snkey;
            newItem.create_man = i.create_man;
            newItem.edit_man = i.edit_man;
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          this.sortBy();
        });
    },
    sortBy() {
      var matchData = this.viewData.sort(function (a, b) {
        return a.date < b.date ? 1 : -1;
      });
      this.viewData = matchData.sort(function (a, b) {
        if (a.date == b.date) {
          return a.time < b.time ? 1 : -1;
        }
      });
    },
    //設定修改及刪除的權限為;非創建者就無法修改或刪除
    checkAuth(item) {
      if (this.$store.state.pData.master_key == 'true'){
        return true;
      }else{
        return item.create_man.includes(this.$store.state.pData.username);
      }
    },
    edit(item) {
      //修改功能
      if (this.checkAuth(item)) {
        this.$refs.editProcess.$emit("fromParent", item);
      } else {
        var pop = {
          msg: "非紀錄人無法修改或刪除!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    del(delData) {
      //刪除功能
      if (this.checkAuth(delData)) {
        this.$confirm("確認刪除?").then((res) => {
          delData.delman =
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";
          var postData = {
            snkey: delData.snkey,
            tablename: "nursingrecord",
            info: JSON.stringify(delData),
          };

          if (res) {
            this.axios
              .post(
                "general/delv3/" +
                  this.$store.state.databaseName +
                  "/nursingrecord",
                qs.stringify(postData)
              )
              .then((rs) => {
                // console.log(rs.data);
                if (rs.data.state == 1) {
                  var pop = { msg: "已刪除", type: true, theme: "success" };
                  this.$store.commit("snackbar", pop);
                  this.getAllData();
                }
              });
          }
        });
      } else {
        var pop = {
          msg: "非紀錄人無法修改或刪除!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach((key) => {
        if (key != "") {
          outputitems = outputitems.filter((item) => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    },
    print() {
      this.$refs.enterEdit.$emit("childFn", this.items);
    },
  },
};
</script>


<style lang="scss">
.nursingrecordlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>