<template>
  <div class="selectlist">
    <v-dialog v-model="dialog" width="70vw">
      <!-- <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>-->

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-alert class="mt-2" color="primary" outlined>
            <v-list>
              <v-list-item-group color="primary">
                <v-list-item v-for="(item, i) in items" :key="i" @click.stop="insertSelect(item)">
                  <v-list-item-avatar>
                    <v-icon class="primary white--text">mdi-check-bold</v-icon>
                  </v-list-item-avatar>

                  <v-list-item-content>
                    <v-list-item-title>
                      {{item.content}}
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-alert>

          <!-- <pre>{{items}}</pre> -->
        </v-card-text>

        <!-- <pre>{{      inspectionreportItems}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
      dialog: false,
      title: "",
      titleStyle: "",
    };
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("childFn", function (items) {
      this.dialog = true;
      this.enterProcess(items);
    });
  },

  methods: {
    //進入畫面時
    enterProcess(items) {
      this.title = "資料選擇";
      this.titleStyle = "font-weight-black primary lighten-2";
      // this.items = items;
      this.items = this.createitems(items);
    },
    createitems(items) {
      //創建為"medicalRecord"  就診紀錄
      if (items.database == "medicalRecord") {
        console.log('select medicalRecord');
        this.title = "資料選擇-就診紀錄";
        return items.matchItems.map((i) => {
          return {
            database : items.database,
            data: i,
            content: `${i.date} / ${i.hospital_name} / ${i.branch_name} / ${i.doctor_name} / ${i.main_cause_of_treatment_name}`,
          };
        });
      }


      //創建為"signLife"  生命徵象
      if (items.database == "signLife") {
        console.log('select signLife');
        this.title = "資料選擇-生命徵象";
        return items.matchItems.map((i) => {
          return {
            database : items.database,
            data: i,
            content: `${i.date} / ${i.time} / ${i.create_man}`,
          };
        });
      }

      //創建為"nursingPlan"  護理計畫
      if (items.database == "nursingPlan") {
        console.log('select nursingPlan');
        this.title = "資料選擇-護理計畫";
        return items.matchItems.map((i) => {
          return {
            database : items.database,
            data: i,
            content: `${i.date} / ${i.stop_date} / ${i.question}`,
          };
        });
      }

      //創建為"inspectionreport"  檢驗報告單
      if (items.database == "inspectionreport") {
        console.log('select inspectionreport');
        this.title = "資料選擇-檢驗報告單";
        return items.matchItems.map((i) => {
          return {
            database : items.database,
            data: i,
            content: `${i.date} / ${i.location} / ${i.create_man}`,
          };
        });
      }

      //創建為"pipeline_used"  管路使用紀錄
      if (items.database == "pipeline_used") {
        console.log('select pipeline_used');
        this.title = "資料選擇-目前使用中管路";
        return items.matchItems.map((i) => {
          return {
            database : items.database,
            data: i,
            content: `${i.name} / ${i.unit} / ${i.start_date} ~ ${i.end_date} / ${i.create_man} / ${i.note}`,
          };
        });
      }

      //創建為"consultation"  轉介照會單
      if (items.database == "consultation") {
        console.log('select consultation');
        this.title = "資料選擇-轉介照會單";
        // console.log(items.matchItems)
        return items.matchItems.map((i) => {
          return {
            database : items.database,
            data: i,
            content: `${i.date} / ${i.nurse_name} /${i.create_man}`,
          };
        });
      }


    },
    insertSelect(item){
      // console.log(item);
      this.dialog = false;
      this.$emit("insertSelect",item);
    },
  },
};
</script>