<template>
  <div class="nursingrecordadd">
    <v-dialog v-model="dialog" width="auto">
      <SelectList ref="enterProcess" @insertSelect="insertSelect"></SelectList>

      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <v-row>
              <v-col cols="3">
                <v-text-field label="評估日期" v-model="list.date" type="date" :rules="emptyRules"></v-text-field>
              </v-col>
              <v-col cols="3">
                <v-text-field label="評估時間" v-model="list.time" type="time" :rules="emptyRules"></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-select :items="projectItems" label="項目" v-model="list.project"></v-select>
              </v-col>
              <v-col cols="2">
                <v-checkbox class="pa-2 error white-font" label="重點交班設定" color="white" v-model="list.focus" value="true" hide-details></v-checkbox>
              </v-col>
              <v-col cols="12" class="d-flex justify-right align-center">
                
                <v-spacer></v-spacer>
                <v-btn class="primary mx-1" @click.stop="openSelectList('medicalRecord')">載入就診紀錄</v-btn>
                <v-btn class="primary mx-1" @click.stop="openSelectList('signLife')">載入生命徵象</v-btn>
                <v-btn class="primary mx-1" @click.stop="openSelectList('nursingPlan')">載入護理計畫</v-btn>
                <v-btn class="primary mx-1" @click.stop="openSelectList('inspectionreport')">載入檢驗報告單</v-btn>
                <v-btn class="primary mx-1" @click.stop="openSelectList('pipeline_used')">載入管路使用紀錄</v-btn>
                <v-btn class="primary mx-1" @click.stop="openSelectList('consultation')">載入轉介照會單紀錄</v-btn>
                <!-- <v-btn class="primary mx-1" @click.stop="insertMedicial">載入就診紀錄</v-btn>
                <v-btn class="primary mx-1" @click.stop="insertSignLife">載入生命徵象</v-btn>
                <v-btn class="primary mx-1" @click.stop="insertNursingPlan">載入護理計畫</v-btn>
                <v-btn class="primary mx-1" @click.stop="insertInspectionreport">載入檢驗報告單</v-btn> -->
              </v-col>

              <v-col cols="12">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>護理紀錄內容</h2>
                  </v-sheet>
                  <v-row>
                    <v-col cols="12">
                      <v-textarea rows="15" label="護理紀錄內容" v-model="list.nursingrecord_content"></v-textarea>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-col>

            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'" :loading="loading" :disabled="loading">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'" :loading="loading" :disabled="loading">確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{ list }}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";
import SelectList from "@/components/SelectList";

export default {
  components: { SelectList },
  data() {
    return {
      dialog: false,
      list: { }, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"],
      medicalRecordItems: [], //病歷資料暫存
      signLifeItems: [], //生命徵象資料暫存
      nursingPlanItems: [], //護理計畫資料暫存
      inspectionreportItems: [], //檢驗報告單暫存
      pipeline_usedItems:[],  //管路使用紀錄暫存
      consultationItems:[],  //轉介照會單紀錄暫存
      projectItems: [
        "",
        "跌倒",
        "住院出院紀錄",
        "護理評值",
        "入住紀錄",
        "住院後返本家",
        "移除紀錄",
        "聯繫紀錄",
        "照護計畫"
      ],
      loading : false,
    };
  },
  
  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function(item) {
      this.dialog = true;
      this.editProcess(item);
    });
    this.getMedical(); //取得就診資料
    this.getSignLife(); //取得就診資料
    this.getNursingPlan(); //取得護理計畫資料
    this.getInspectionreport(); //取得檢驗告資料
    this.getPipeline_used(); //取得檢驗告資料
    this.getConsultation(); //取得檢驗告資料
  },

  methods: {
    //進入畫面時,執行的是新增作業
    addProcess() {
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";

      //設定最後一筆資料為新增時的預設值及清除新增中不必要的值
      // if (this.viewData[0] != undefined) {
      //   this.list = { ...this.viewData[0] };
      // }
      this.list = {
        snkey: "",
        date: dayjs().format("YYYY-MM-DD"),
        nursingrecord_content: "",
        time : dayjs().format("HH:mm"),
        edit_man : ""
      }
      
    },
    //進入畫面時,執行的是修改作業
    editProcess(item) {
      
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容
      // this.$set(this.list,'date',"");
    },
    //確認新增
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        var postData = {
          u_snkey: this.$store.state.uData.snkey,
          date: this.list.date,
          datalist: JSON.stringify(this.list),
          create_man:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")" //執行動作的人,時間,內容
        };

        this.loading = true;
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/nursingrecord",
            qs.stringify(postData)
          )
          .then(rs => {
            // console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    //確認修改
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        var postData = {
          snkey: this.list.snkey,
          date: this.list.date,
          datalist: JSON.stringify(this.list),
          edit_man:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")" //執行動作的人,時間,內容
        };

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/nursingrecord",
            qs.stringify(postData)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                if (this.oldList[i] == undefined) {
                  this.$set(this.oldList, i, "");
                }
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    getMedical() {
      //取得該病歷的就醫紀錄
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios.post("medicinerecord/getByUserSnkey/" + this.$store.state.databaseName + "/" + this.$store.state.uData.snkey)
        .then(rs => {
          this.medicalRecordItems = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getSignLife() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/signlife",
          qs.stringify(postData)
        )
        .then(rs => {
          // console.log(rs.data)
          this.signLifeItems = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getNursingPlan() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/nursingplan",
          qs.stringify(postData)
        )
        .then(rs => {
          // console.log(rs.data)
          this.nursingPlanItems = rs.data.map(i=>{
            return JSON.parse(i.datalist);
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getInspectionreport() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/inspectionreport",
          qs.stringify(postData)
        )
        .then(rs => {
          // console.log(rs.data)
          this.inspectionreportItems = rs.data.map(i=>{
            return JSON.parse(i.datalist);
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getPipeline_used() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/pipeline_used",
          qs.stringify(postData)
        )
        .then(rs => {
          // console.log(rs.data)
          this.pipeline_usedItems = rs.data.map(i=>{
            var newItem = JSON.parse(i.datalist);
            newItem.create_man = i.create_man;
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getConsultation() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/consultation",
          qs.stringify(postData)
        )
        .then(rs => {
          // console.log(rs.data)
          this.consultationItems = rs.data.map(i=>{
            var newItem = JSON.parse(i.datalist);
            newItem.create_man = i.create_man;
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    openSelectList(database){
      // console.log(this[database+'Items']);
      var matchItems = this[database+'Items'].filter(i => {
        return i.date == this.list.date;
      });

      //管路使用的情況下,不再只找出當日,而是取得還在使用中的管路
      if (database == 'pipeline_used'){
        matchItems = this[database+'Items'].filter(i => {
        return (this.list.date >= i.start_date && this.list.date <= i.end_date);
      });
      }
// console.log(matchItems);
      var postData = {
        database : database,
        matchItems: matchItems
      }  

      if (matchItems.length > 0) {
        this.$refs.enterProcess.$emit("childFn", postData);
      }else{
        var pop = {
          msg: "暫無符合的紀錄",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }  
    },
    insertSelect(data){
      //載入就診紀錄
      if (data.database == "medicalRecord"){
        const item = data.data;
        this.list.nursingrecord_content +=
          "[就診紀錄內容]" + "\n" +"就診日期:" + item.date + "; 診別:" +
          item.outpatient + "; 時段:" + item.time + "; 醫院:" + item.hospital_name +
          "; 科別:" + item.branch_name + "; 醫生:" + item.doctor_name + "; 就診主因:" +
          item.main_cause_of_treatment_name + "; 回診日期:" + item.back_date + "; 使用車輛:" +
          item.car + "; 陪診人員:" + item.accompany + "; 管路:" + item.pipeline + "; 管路到期日:" +
          item.pipelinedate + "; 攜帶物品:" + item.carryitems + "\n\n" + "原因狀況描述:" +
          "\n" + item.description + "\n\n" + "就醫情形回覆:" + "\n" + item.response;
      }

      //載入生命徵象
      if (data.database == "signLife"){
        const item = data.data;
        this.list.nursingrecord_content += "[生命徵象內容]" + "\n";

        if (item.time != "") { this.list.nursingrecord_content += "評估時間:" + item.time + "; "; }
        if (item.result != "") { this.list.nursingrecord_content += "檢查結果:" + item.result + "; "; }
        if (item.weight != "") { this.list.nursingrecord_content += "體重:" + item.weight + "; "; }
        if (item.bp_h != "") { this.list.nursingrecord_content += "血壓(H):" + item.bp_h + "; "; }
        if (item.bp_l != "") { this.list.nursingrecord_content += "血壓(L):" + item.bp_l + "; "; }
        if (item.pulse != "") { this.list.nursingrecord_content += "脈搏:" + item.pulse + "; "; }
        if (item.temperature != "") { this.list.nursingrecord_content += "體溫:" + item.temperature + "; "; }
        if (item.breath != "") { this.list.nursingrecord_content += "呼吸:" + item.breath + "; "; }
        if (item.blood_sugar != "") { this.list.nursingrecord_content += "血糖:" + item.blood_sugar + "; "; }
        if (item.blood_oxygen != "") { this.list.nursingrecord_content += "血氧濃度:" + item.blood_oxygen + "; "; }
        if (item.pain != "") { this.list.nursingrecord_content += "疼痛指數:" + item.pain + "; "; }
        if (item.gcs_e != "") { this.list.nursingrecord_content += "GCS-E:" + item.gcs_e + "; "; }
        if (item.gcs_v != "") { this.list.nursingrecord_content += "GCS-V:" + item.gcs_v + "; "; }
        if (item.gcs_m != "") { this.list.nursingrecord_content += "GCS-M:" + item.gcs_m + "; "; }
        if (item.stool != "") { this.list.nursingrecord_content += "大便次數:" + item.stool + "; "; }
        if (item.note != "") { this.list.nursingrecord_content += "備註:" + item.note + "; "; }
      }

      //載入護理計畫
      if (data.database == "nursingPlan"){
        const item = data.data;
        this.list.nursingrecord_content +=
          "[護理計畫單內容]" + "\n" + "護理診斷:" + "\n" + item.question + "\n\n" + "可能導因:" + "\n" +
          item.relevant + "\n\n" + "護理目標:" + "\n" + item.target + "\n\n" + "護理措施:" +  "\n" +
          item.measures + "\n\n" + "評值說明:" + "\n" + item.plan;
      }

      //載入護理計畫
      if (data.database == "inspectionreport"){
        const item = data.data;
        this.list.nursingrecord_content += "[檢驗報告內容]" + "\n";

        if (item.location != "" && item.location != undefined) {
          this.list.nursingrecord_content += "檢驗地點:" + item.location + "; ";
        }
        if (item.leukocyte != "" && item.leukocyte != undefined) {
          this.list.nursingrecord_content +=
            "白血球:" + item.leukocyte + "; ";
        }
        if (item.erythrocyte != "" && item.erythrocyte != undefined) {
          this.list.nursingrecord_content +=
            "紅血球:" + item.erythrocyte + "; ";
        }
        if (item.hemoglobin != "" && item.hemoglobin != undefined) {
          this.list.nursingrecord_content +=
            "血色素:" + item.hemoglobin + "; ";
        }
        if (item.platelet != "" && item.platelet != undefined) {
          this.list.nursingrecord_content +=
            "血小板:" + item.platelet + "; ";
        }
        if (item.albumin != "" && item.albumin != undefined) {
          this.list.nursingrecord_content +=
            "白蛋白:" + item.albumin + "; ";
        }
        if (item.glycosylatedhemoglobin != "" && item.glycosylatedhemoglobin != undefined) {
          this.list.nursingrecord_content +=
            "醣化血色素:" + item.glycosylatedhemoglobin + "; ";
        }
        if (item.got != "" && item.got != undefined) {
          this.list.nursingrecord_content +=
            "GOT(AST):" + item.got + "; ";
        }
        if (item.gpt != "" && item.gpt != undefined) {
          this.list.nursingrecord_content +=
            "GPT(ALT):" + item.gpt + "; ";
        }
        if (item.bloodsugar != "" && item.bloodsugar != undefined) {
          this.list.nursingrecord_content +=
            "血糖:" + item.bloodsugar + "; ";
        }
        if (item.cholesterol != "" && item.cholesterol != undefined) {
          this.list.nursingrecord_content +=
            "膽固醇:" + item.cholesterol + "; ";
        }
        if (item.tg != "" && item.tg != undefined) {
          this.list.nursingrecord_content += "TG:" + item.tg + "; ";
        }
        if (item.bun != "" && item.bun != undefined) {
          this.list.nursingrecord_content += "BUN:" + item.bun + "; ";
        }
        if (item.cre != ""  && item.cre != undefined) {
          this.list.nursingrecord_content += "CRE:" + item.cre + "; ";
        }
        if (item.ua != "" && item.ua != undefined) {
          this.list.nursingrecord_content +=
            "UA:" + item.ua + "; ";
        }
        if (item.na != "" && item.na != undefined) {
          this.list.nursingrecord_content += "Na:" + item.na + "; ";
        }
        if (item.k != "" && item.k != undefined) {
          this.list.nursingrecord_content += "K:" + item.k + "; ";
        }
        if (item.hdlldl != "" && item.hdlldl != undefined) {
          this.list.nursingrecord_content +=
            "HDL/LDL:" + item.hdlldl + "; ";
        }
        if (item.ph != "" && item.ph != undefined) {
          this.list.nursingrecord_content += "尿液:" + item.ph + "; ";
        }
        if (item.urineprotein != "" && item.urineprotein != undefined) {
          this.list.nursingrecord_content +=
            "尿液 尿蛋白 (-):" + item.urineprotein + "; ";
        }
        if (item.ob != "" && item.ob != undefined) {
          this.list.nursingrecord_content += "尿液 OB (-):" + item.ob + "; ";
        }
        if (item.rbc != "" && item.rbc != undefined) {
          this.list.nursingrecord_content += "尿液 RBC:" + item.rbc + "; ";
        }
        if (item.wbc != "" && item.wbc != undefined) {
          this.list.nursingrecord_content += "尿液 WBC:" + item.wbc + "; ";
        }
        if (item.bacteria != "" && item.bacteria != undefined) {
          this.list.nursingrecord_content +=
            "尿液 Bacteria (-):" + item.bacteria + "; ";
        }
        if (item.glu != "" && item.glu != undefined) {
          this.list.nursingrecord_content += "尿液 GLU (-):" + item.glu + "; ";
        }
        if (item.urine != "" && item.urine != undefined) {
          this.list.nursingrecord_content +=
            "細菌培養 Urine:" + item.urine + "; ";
        }
        if (item.sputum != "" && item.sputum != undefined) {
          this.list.nursingrecord_content +=
            "細菌培養 Sputum:" + item.sputum + "; ";
        }
        if (item.note != "" && item.note != undefined) {
          this.list.nursingrecord_content +=
            "其他備註:" + item.note + "; ";
        }
        if (item.dispose != "" && item.dispose != undefined) {
          this.list.nursingrecord_content += "處置:" + item.dispose + "; ";
        }
        if (item.track != "" && item.track != undefined) {
          this.list.nursingrecord_content += "追蹤:" + item.track + "; ";
        }
      }


      //載入管路使用紀錄
      if (data.database == "pipeline_used"){
        const item = data.data;
        this.list.nursingrecord_content +=
          "[管路使用紀錄]" + "\n" + "使用管路:" + item.name + "單位:" + item.unit + " 日期:" + item.start_date + " ~ " + item.end_date + "備註:" + item.note;
      }

      //載入轉介照會單
      if (data.database == "consultation"){
        const item = data.data;
        this.list.nursingrecord_content += "\n[轉介照會單內容]\n" + "預計照會人員:";

        if (item.doctor) {
          this.list.nursingrecord_content += "醫生" + "; ";
        }
        if (item.nutritionist) {
